<template>
    <v-containter>
        <template>
            <v-toolbar flat color="white">
                <v-toolbar-title>Crud Atma pedia</v-toolbar-title>
                <v-divider class="mx-4" inset vertical></v-divider>
                <v-spacer></v-spacer>
                <v-text-field label="search" v-model="search"></v-text-field>
                 <v-spacer></v-spacer>

                <v-dialog v-model="dialog" max-width="500px">
                    <template v-slot:activator="{ on }">
                        <v-btn color="primary" dark class="mb-2" v-on="on">buat minuman</v-btn>
                    </template>
                    <v-card>
                        <v-card-title>
                            <span class="headline">{{ formTitle }}</span>
                        </v-card-title>
                        <v-card-text>
                            <v-container>
                                <v-row>
                                    <v-col cols="12" sm="6" md="4">
                                        <v-text-field v-model="editedItem.name" label="Dessert name"></v-text-field>
                                    </v-col>
                                    <v-col cols="12" sm="6" md="4">
                                        <v-select
                                            :items="items"
                                            :menu-props="{ top: true, offsetY: true }"
                                            label="Hot / Cold"
                                            v-model="editedItem.hotcold"
                                        ></v-select>
                                    </v-col>
                                    <v-col cols="12" sm="6" md="4">
                                        <v-text-field v-model="editedItem.fat" label="Fat (g)"></v-text-field>
                                    </v-col>
                                    <v-col cols="12" sm="6" md="4">
                                        <v-text-field v-model="editedItem.carbs" label="Carbs (g)"></v-text-field>
                                    </v-col>
                                    <v-col cols="12" sm="6" md="4">
                                        <v-text-field v-model="editedItem.protein" label="Protein (g)"></v-text-field>
                                    </v-col>
                                </v-row>
                            </v-container>
                        </v-card-text>
                        <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn color="blue darken-1" text @click="close">Cancel</v-btn>
                            <v-btn color="blue darken-1" text @click="save">Save</v-btn>
                        </v-card-actions>
                    </v-card>
                </v-dialog>
                <v-dialog v-model="deleteButton" max-width="500px">
                    <template v-slot:activator="{ off }">
                        <v-btn color="error" dark class="mb-2" @click="multipledelete()">multiple delete {{ selected.length }}</v-btn>
                    </template>
                </v-dialog>
            </v-toolbar>
        </template>
        <v-row>
            <v-col cols="2" v-for="beverage in beverages" :key="beverage.name">
                <v-card
                outlined
                class="d-inline-flex" 

            >
                <v-list-item three-line>
                <v-list-item-content>
                    <div class="overline mb-4">MINUMAN</div>
                    <v-list-item-title class="headline mb-1">{{beverage.name}}</v-list-item-title>
                    <v-list-item-subtitle>Hot/Cold: {{beverage.hotcold}}</v-list-item-subtitle>
                    <v-list-item-subtitle>Fat: {{beverage.fat}}</v-list-item-subtitle>
                    <v-list-item-subtitle>Carbs: {{beverage.carbs}}</v-list-item-subtitle>
                    <v-list-item-subtitle>Protein: {{beverage.protein}}</v-list-item-subtitle>
                    
                    <v-btn color="success" @click="edititem(beverage)"> Edit</v-btn>
                    <v-btn color="error" @click="deleteitem(beverage)"> Delete</v-btn>
                </v-list-item-content>
                </v-list-item>
            </v-card>
        </v-col>
        </v-row>
    </v-containter>
</template>
<script>
import { beveragesRef } from '../firebase';

    export default {
        data: () => ({
            selected: [],
            cek : -1,
            search: "",
            items: ['Hot', 'Cold'],
            dialog: false,
            deleteButton: false,
            beverages: [],
            editedIndex: -1,
            editedItem: {
                name: '',
                hotcold: 0,
                fat: 0,
                carbs: 0,
                protein: 0,
            },
            defaultItem: {
                name: '',
                hotcold: 0,
                fat: 0,
                carbs: 0,
                protein: 0,
            },
        }),
        firebase: {
            beverages : beveragesRef
        },
        computed: {
            formTitle() {
                return this.cek === -1 ? 'New Item' : 'Edit Item'
            },
        },
        watch: {
            dialog(val) {
                val || this.close()
            },
        },
        methods: {
            close() {
                this.clear()
                this.dialog = false
            },
            // methods untuk save
            save() {
                if (this.cek > -1) {
                     beveragesRef.child(this.editedIndex).set({name: this.editedItem.name,hotcold: this.editedItem.hotcold, fat : this.editedItem.fat, carbs: this.editedItem.carbs, protein: this.editedItem.protein})         
                     this.clear() 
                } else {
                     beveragesRef.push({name: this.editedItem.name,hotcold: this.editedItem.hotcold, fat : this.editedItem.fat, carbs: this.editedItem.carbs, protein: this. editedItem.protein})
                    this.clear()
                }
                this.close()
            },
            clear(){
                this.editedItem={}
                this.editedItem.name=''
                this.editedItem.hotcold= ''
                this.editedItem.fat= 0
                this.editedItem.carbs= 0
                this.editedItem.protein= 0
                this.cek=-1
            },
            edititem(item) {
                this.editedIndex = item['.key']
                this.cek = 0
                this.editedItem = Object.assign({},item)
                this.dialog = true
            },
            deleteitem(item) {
                confirm(item) && beveragesRef.child((item['.key'])).remove()
            },
            deleteitem2(item) {
                beveragesRef.child((item['.key'])).remove()
            },
            multipledelete(){
                for(var i = this.selected.length - 1 ; i>=0; i--){
                    this.deleteitem2(this.selected[i])
                }
                this.selected=[]
            }
        },
    } </script>